
import './Value.css'


const Value = ({name, val, add, minus, index}) => {

    const a = () => {
        add(index)
    }

    const m = () => {
        minus(index)
    }

    return (
        <div className='valueHolder'>
            <div className='value'>
                <div className='valueName'>{name}</div>
                <div className='actualValue'>{val}</div>
            </div>
            <div className='justify'>
                <button className='justifyBtn' onClick={a}>+</button>
                <button className='justifyBtn' onClick={m}>-</button>
            </div>
        </div>)
}

export default Value