const att = [["力量",21],["敏捷",10],["体质",12],["智力",8],["感知",20],["魅力",15],["护甲AC",22],["速度",30],["当前血量",39],["血量上限",39],["临时生命上限",0],["死亡豁免成功",0],["死亡豁免失败",0]]






const Attribute = () => {

    //数据
    const [data, setData] = useState(att)

    


    const add = (index) => {
        const newList = [...data]
        newList[index][1] += 1
        setData(newList)
    }

    const minus = (index) => {
        const newList = [...data]
        newList[index][1] -= 1
        setData(newList)    
    }

    return (
        <>
            <div className='attributeTitle'>人物属性</div>
            <div className='attributeValueHolder'>

                {
                    data.map(
                        (item, index) => {
                            // console.log(value, key)
                            return <Value key={index} name={item[0]} val={item[1]} index={index} add={add} minus={minus}/>
                        }
                    )
                }

            </div>

            
            
        </>
        
    )


}

export default Attribute